<template>
  <van-cell class="demand-card" @click="handleClick">
    <template slot="title">
      <div class="title">
        <van-icon class-prefix="tj-icon" name="cube" />
        <van-tag
          v-if="demand.is_quality === '1'"
          style="vertical-align: middle; margin-top: -3px"
          color="#c20026"
          >优质</van-tag
        >
        {{ demand.type_name }} | {{ demand.title }}
      </div>
      <div class="content">
        <van-icon class-prefix="tj-icon" name="tuceng" />
        <span v-html="demand.describe"></span>
      </div>
      <div class="location">
        <van-icon class-prefix="tj-icon" name="biaodiandidian" />
        {{ demand.address }}
      </div>
    </template>
    <div class="time">
      {{ moment.unix(demand.addtime).format("YYYY-MM-DD") }}
    </div>
    <van-button
      v-if="demand.demand_status === '0'"
      class="action-btn waiting"
      size="small"
      >待审核</van-button
    >
    <van-button
      v-if="demand.demand_status === '1'"
      class="action-btn active"
      size="small"
      >待沟通</van-button
    >
    <van-button
      v-if="demand.demand_status === '2'"
      class="action-btn disable"
      size="small"
      >已解决</van-button
    >
    <van-button
      v-if="demand.demand_status === '3'"
      class="action-btn disable"
      size="small"
      >未通过</van-button
    >
    <van-button
      v-if="demand.status === '4'"
      class="action-btn disable"
      size="small"
      >已过期</van-button
    >
    <div class="view">点击量：{{ demand.visit }}</div>
  </van-cell>
</template>

<script>
import moment from "moment";
import { Cell, Icon, Button, Tag } from "vant";

export default {
  name: "demand-item",
  props: ["demand"],
  components: {
    [Button.name]: Button,
    [Cell.name]: Cell,
    [Tag.name]: Tag,
    [Icon.name]: Icon
  },
  data() {
    return {
      moment
    };
  },
  methods: {
    handleClick() {
      this.$emit("click");
    }
  }
};
</script>

<style lang="less" scoped>
.demand-card {
  .title,
  .content,
  .location,
  .time,
  .view,
  .action-btn {
    line-height: 24px;
    height: 24px;
    box-sizing: border-box;
  }

  .action-btn {
    border-radius: 4px;
    color: #fff;

    &.active {
      background: #ff6e1d;
    }

    &.disable {
      background-color: #ccc;
    }

    &.waiting {
      background-color: #2ec5d2;
    }
  }

  .tj-icon {
    margin-right: 5px;
  }

  .title,
  .content,
  .location {
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .title {
    color: #3f3f3f;
    font-size: 16px;

    .tj-icon {
      color: #c20026;
    }
  }

  .content,
  .location {
    color: #999;
    font-size: 14px;
  }

  .action-btn {
    width: 67px;
  }

  .time,
  .view {
    transform: scale(0.8);
    text-align: right;
  }

  .time {
    transform-origin: top right;
  }

  .view {
    transform-origin: bottom right;
  }
}

@media (min-width: 750px) {
  .demand-card {
    .title,
    .content,
    .location,
    .time,
    .view,
    .action-btn {
      line-height: 24px;
      height: 24px;
    }

    .action-btn {
      border-radius: 4px;
    }

    .tj-icon {
      margin-right: 5px;
    }

    .title {
      font-size: 16px;
    }

    .content,
    .location {
      font-size: 14px;
    }

    .action-btn {
      width: 67px;
    }
  }
}
</style>
